.object-table{
    width: 92%;
    margin-left: 30px;
    margin-top: 15px;
    border-collapse: collapse;
}

tr {
    height: 40px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    font-family: 'Roboto';
}

.loader-table {
    width: 97%;
    min-height: 70vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.page-pagination{
    margin-left: 30px;
    margin-top: 20px;
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.page-pagination p{
    font-size: 15px;
    font-family: 'Roboto';
}

.page-pagination__pagination{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 50%;
}

.page-pagination__pagination p {
    cursor: pointer;
}

.page-pagination__pagination ul {
    list-style: none;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0;
    border-radius: 4px;
}

.page-pagination__pagination ul li {
    padding: 0.9vh 0.7vw;
    border: 1px solid #C4C4C4;
    border-left: none;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 0.9em;
    cursor: pointer;
    border-radius: 0;
}

.page-pagination__pagination ul li:hover{
    border: 1px solid #731919;
    border-left: none;
    background: #731919;
    color: white;
}

.page-pagination__pagination ul li:first-of-type{
    border-left: 1px solid #C4C4C4;
    border-radius: 4px 0 0 4px;
}

.page-pagination__pagination ul li:last-of-type{
    border-radius: 0 4px 4px 0;
}

.selected {
    background: #731919;
    outline: none;
    color: white;

}

.page-pagination a {
    outline: none;
}
.all-object-page{
    display: flex;
    flex-direction: column;
}

.all-object__loader{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.categories-item a {
    text-decoration: none;
}

.categories-item{
    cursor: pointer;
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 15px 0;
    color: #731919;
    border: 3px solid #731919;
    box-sizing: border-box;
    box-shadow: 0 1px 6px rgba(0, 0, 0, 0.15);
    border-radius: 4px;
    font-family: 'Roboto';
}

.categories-item h5{
    height: 100%;
    width: 100%;
    align-items: center;
    justify-content: center;
    display: flex;
    color: #731919;
    font-size: 17px;
    font-weight: 500;
    transition: background-color 0.3s;
}

.categories-item h5 a {
    color: #731919;
    display: block;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.categories-item h5:hover{
    background: #731919;
}

.categories-item h5:hover a{
    color: white;
}
.categories-list {
    width: 32%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.categories-list p{
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
}
.categories-page{
    display: flex;
    flex-direction: column;
    transition: 0.4s ease-out;
}

.categories-page__categories{
    margin-left: 1.4vw;
    width: 95%;
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
}
.signin-form{
    width: 35vw;
    min-height: 300px;
    border: 1px solid #C4C4C4;
    border-radius: 3px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.signin__options{
    margin-top: 40px;
    margin-bottom: 20px;
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.signin__options a {
    text-decoration: none;
}

.signin__options a:visited{
    text-decoration: none;
}

.signin__options span {
    margin-right: 30px;
    font-family: Roboto;
    font-weight: normal;
    font-size: 16px;
    cursor: pointer;
}

.signin__options span:hover{
    color:#c4c4c4;
    transition: 0.2s;
}

.signin-from__loader{
    width: 100%;
    height: 60px;
    margin-top: -10px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.sign-in-page{
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
}
.reset-password-form{
    width: 35vw;
    min-height: 300px;
    border: 1px solid #C4C4C4;
    border-radius: 3px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.reset-password__options{
    margin-top: 40px;
    margin-bottom: 20px;
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.reset-password__options a {
    text-decoration: none;
}

.reset-password__options span {
    margin-right: 30px;
    font-family: Roboto;
    font-weight: normal;
    font-size: 16px;
    cursor: pointer;
}

.reset-password__options span:hover{
    color:#c4c4c4;
    transition: 0.2s;
}

.reset-password__loader{
    width: 100%;
    height: 60px;
    margin-top: -10px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.reset-password-page{
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
}
.aside-button {
    cursor: pointer;
    width: 100%;
    height: 55px;
    border: none;
    outline: none;
    background: transparent;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.aside-button a {
    width:100%;
    height: 100%;
    display: flex;
    align-items: center;
    text-align: left;
    text-decoration: none;
    font-family: Roboto;
    font-style: normal;
    color: black;
    font-weight: 500;
    font-size: 16px;
    transition: 0.15s;
}

.aside-button.selected{
    background: #731919;
}

.aside-button.selected a {
    color: white;
}

.aside-button:last-of-type{
    margin-top: 27vh;
}
.full-menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 18vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    transform: translateX(0);
}

/* aside animation */
.open-slide-in-left {
    transform: translateX(-100%);
    transition: 0.4s ease-out;
}

.open-slide-in-right {
    transform: translateX(0);
    transition: 0.4s ease-out;
}

.full-menu p {
    margin-top: 40px;
    margin-bottom: 92px;
}

.full-menu a {
    text-decoration: none;
    color: black;
}

/* logo animation */
.logo-in-left {
    transform: translateX(-100%);
    transition: 0.4s ease-out;
}

.logo-in-right {
    transform: translateX(0);
    transition: 0.4s ease-out;
}

.full-menu .menu-btn {
    cursor: pointer;
    margin-right: 15px;
}

.full-menu .option-btn {
    color: black;
    margin: 0 10px;
}

.close-menu {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9;
    width: 5vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    transform: translateX(-100%);
    background-color: white;
}

/* aside animation */
.close-slide-in-left {
    transform: translateX(-100%);
    transition: 0.4s ease-out;
}

.close-slide-in-right {
    transform: translateX(0);
    transition: 0.4s ease-out;
}

.close-menu p {
    margin-top: 40px;
    margin-bottom: 92px;
}


.close-menu .menu-btn {
    cursor: pointer;
    margin-right: 15px;
}

.close-menu .option-btn {
    color: black;
    margin: 0 10px;
}


.app {
  display: flex;
  justify-content: space-between;
}
body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

